"use client";
import React, { useState, useEffect } from "react";
import {
  Box,
  Input,
  Textarea,
  Button,
  VStack,
  HStack,
  Heading,
  Spinner,
  Alert,
  Code,
} from "@chakra-ui/react";

const API_URL = "http://127.0.0.1:8000"; // Change to your backend URL if needed

export default function Home() {
  const [openaiKey, setOpenaiKey] = useState("");
  const [query, setQuery] = useState("");
  const [result, setResult] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Load OpenAI key from localStorage on mount
  useEffect(() => {
    const storedKey = localStorage.getItem("openai_api_key") || "";
    setOpenaiKey(storedKey);
  }, []);

  // Save OpenAI key to localStorage when it changes
  useEffect(() => {
    if (openaiKey) {
      localStorage.setItem("openai_api_key", openaiKey);
    }
  }, [openaiKey]);

  const handleSubmit = async (endpoint: "similar" | "rag") => {
    setLoading(true);
    setError(null);
    setResult(null);
    try {
      const res = await fetch(`${API_URL}/${endpoint}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ query, openai_api_key: openaiKey }),
      });
      if (!res.ok) {
        const err = await res.json();
        throw new Error(err.detail || "Unknown error");
      }
      const data = await res.json();
      setResult(data);
    } catch (err: any) {
      setError(err.message || "Request failed");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box minH="100vh" bg="gray.50" display="flex" alignItems="center" justifyContent="center" p={4}>
      <Box w="full" maxW="xl" bg="white" rounded="md" shadow="md" p={8}>
        <VStack spacing={6} align="stretch">
          <Heading size="lg" textAlign="center">Cuttlefish Search</Heading>
          <Box>
            <Box as="label" fontWeight="medium" mb={1} display="block">OpenAI API Key</Box>
            <Input
              type="password"
              value={openaiKey}
              onChange={e => setOpenaiKey(e.target.value)}
              placeholder="sk-..."
              autoComplete="off"
            />
          </Box>
          <Box>
            <Box as="label" fontWeight="medium" mb={1} display="block">Query</Box>
            <Textarea
              value={query}
              onChange={e => setQuery(e.target.value)}
              placeholder="Enter your query here..."
              minH="100px"
            />
          </Box>
          <HStack spacing={4}>
            <Button
              colorScheme="blue"
              flex={1}
              onClick={() => handleSubmit("similar")}
              isLoading={loading}
              isDisabled={!openaiKey || !query}
            >
              Similarity
            </Button>
            <Button
              colorScheme="green"
              flex={1}
              onClick={() => handleSubmit("rag")}
              isLoading={loading}
              isDisabled={!openaiKey || !query}
            >
              RAG
            </Button>
          </HStack>
          {loading && (
            <Box textAlign="center"><Spinner size="lg" color="blue.500" /></Box>
          )}
          {error && (
            <Alert status="error">
              {error}
            </Alert>
          )}
          {result && (
            <Box mt={4}>
              <Heading size="md" mb={2}>Results</Heading>
              <Box bg="gray.100" p={3} rounded="md" overflowX="auto">
                <Code whiteSpace="pre" fontSize="sm">
                  {JSON.stringify(result, null, 2)}
                </Code>
              </Box>
            </Box>
          )}
        </VStack>
      </Box>
    </Box>
  );
}
